import React, {useState,useEffect,Fragment,useRef,memo} from 'react';
import './ChatArea.scss';
import OuterChatList from "../OuterChatList/OuterChatList";
import RioChatBox from "../ChatBox/RioChatBox";
import { useSelector, useDispatch } from 'react-redux';

const ChatArea = ({

}) => {
    const { botData } = useSelector(state => state.chats);
    const rio = botData;
    return <div className='ChatAreaContainer'>
        {rio.show ? <RioChatBox chatData = {rio}/> : ""}
        <OuterChatList/>
    </div>
}

export default ChatArea;