import React, {useState,useEffect,Fragment,useRef,memo} from 'react';
import Button from '@mui/material/Button';
import './OuterChatList.scss';
import SlideToggle from '../Navigation/SlideToggle';
import Fab from '@mui/material/Fab';
import ChatIcon from '@mui/icons-material/Chat';
import { ReactComponent as LogoIcon } from '../../Assets/ic_logo.svg';
import { useSelector, useDispatch } from 'react-redux';
import {
	actionShowSelectedChatBox,
    actionShowBotChatBox
} from '../../app/Reducers/chats/chatsSlice';

const OuterChatList = ({

}) => {
    const { chatList, botData } = useSelector(state => state.chats);
    const dispatch = useDispatch();


    

    return <div className='OuterChatListContainer'>
        <SlideToggle
        headText={
            <Fab sx={{margin:"0 0 30px auto"}} size="medium" color="primary" aria-label="add">
                <ChatIcon />
            </Fab>
        }
        headTextTrigger={true}
        >
            <div className="chat-list-wrapper">
                <h3 className="chat-header">Chat list</h3>
                <ul className="chat-list">
                    <li
                    className="chat-item"
                    onClick={()=>{dispatch(actionShowBotChatBox(botData._id))}}>
                        <span className="profile"><LogoIcon/></span>
                        <span className="chat-list-name-and-desc">
                            <span className="user-name">{botData.name}</span>
                            {botData.description ? <span className="user-description">{botData.description}</span> : "" }
                        </span>
                        <span className={`online-indicator ${botData.online ? "online" : ""}`}>.</span>
                    </li>

                    {chatList.map(chat => {
                        return (
                            <li
                            key={chat._id}
                            className="chat-item"
                            onClick={()=>{dispatch(actionShowSelectedChatBox(chat._id))}}>
                                <span className="profile"><LogoIcon/></span>
                                <span className="user-name">{chat.name}</span>
                                <span className={`online-indicator ${chat.online ? "online" : ""}`}>.</span>
                            </li>
                        )  
                    })}
                </ul>
            </div>
        </SlideToggle>
    </div>
}

export default OuterChatList;