import Axios from "axios";
import {v1 as uuid} from "uuid";
import youtube from "../../apis/youtube";

const getSubjectSuggestion = (res,suggestedSubjectQuery) => {
    const {links, subjects, response} = res;

    let linkSuggestion = [];
    if(links.length){
        linkSuggestion = links.map(link => {
            return {
                _id: uuid(),
                name: "Big Bro",
                message: link,
                isSender: false,
                withSelection: false,
                isLinkReference: true
            }
        })
    }
    let answer = [];
    if(response.answer){
        answer = [{
            _id: uuid(),
            name: "Big Bro",
            message: response.answer,
            isSender: false,
            withSelection: true
        }]
    }

    return {
        chat_id: "00000000000",
        messages: [
            ...answer,
            ...linkSuggestion,
            {
                _id: uuid(),
                name: "Big Bro",
                message: "Do you need tutorial for this? I may suggest top 3 youtube tutorial links for your inquiry.",
                isSender: false,
                isLink: false,
                withSelection: true
            }
        ],
        subjects: subjects,
        links: links,
        showOptions: true,
        subjectSuggestion: subjects[0]?.at(-1),
        suggestedSubjectQuery
    }
}

const getLinkSuggestion = (res, suggestedSubjectQuery) => {
    const {links, response, subjects} = res;
    const linkChatMessages = links[0].map(link=>{
        return {
            _id: uuid(),
            name: "Big Bro",
            message: link,
            isSender: false,
            withSelection: false,
            isLinkReference: true
        }
    })

    return {
        chat_id: "00000000000",
        messages:[
            {
                _id: uuid(),
                name: "Big Bro",
                message: response.answer,
                isSender: false,
                withSelection: false
            },
            {
                _id: uuid(),
                name: "Big Bro",
                message: "You may check out these links for more information.",
                isSender: false,
                withSelection: false
            },
            ...linkChatMessages
        ],
        subjects: subjects,
        links: links,
        showOptions: false,
        suggestedSubjectQuery
    }
}

export const getYoutubeSuggestion = async query => {

    const response = await youtube.get('/search',{
        params: {
            q: query
        }
    })
    
    if(response.data.items){
        return response.data.items;

    }
    return []
}

export const analize = async (conversationURL, message) => {
        
    // analize using backend data
    const res = await Axios.get(`${conversationURL}/${message}`)

    if(res.data){
        console.log(res.data)
        const {suggestionPriority} = res.data;

        if(suggestionPriority === "subject") {
            return getSubjectSuggestion(res.data,message);
        }

        else if(suggestionPriority === "link"){
            return getLinkSuggestion(res.data,message)
        }

    }
    
}

